export default{
    data(){
        return{
            list: []
        }
    },
    methods:{
        async _GoodsCollectList(){
            let list = await this.$apis.GoodsCollectList()
            if(list.code == 0){
                this.list = list.result
                return
            }
            this.$toast({title: list.msg,icon: 'none'})
        }
    },
    onLoad(){
        this._GoodsCollectList()
    }
}
